@import '_variables';
@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Start styling the attribution */
.attribution { 
    position: fixed;
    bottom: 0rem;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0.7rem; 
    line-height: 1.6; 
    text-align: center; 
    color:hsl(220, 15%, 55%);
    width: 100%;
}
.attribution a { 
    color: var(--Soft-blue);
}
.attribution a:hover{
    color: var(--Cyan);
    transition: color .3s;
}

/* Screen reader class to hide the content visually */
.sr-only {
    position: absolute !important;
    margin: -1px !important;
    -webkit-clip-path: inset(50%) !important;
            clip-path: inset(50%) !important;
    border: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    overflow: hidden !important;
    white-space: nowrap !important;
}

/*General and page initial setup*/
html{
    font-size: 100%;
    font-family: $font;
    scroll-behavior: smooth;
}

img{
    max-width: 100%;
    height: auto;
}

main{
    background-color: $bg-gray;
    max-width: 100vw;
    min-height: 100vh;
    padding-bottom: 2.5rem;
}

.bg-top{
    display: block;
    width: 100%;
    height: 156px;
    background-image: url('../images/bg-header-mobile.svg'),   linear-gradient($dark-cyan, $dark-cyan);
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and(min-width: 1024px) {
        background-image: url('../images/bg-header-desktop.svg'),   linear-gradient($dark-cyan, $dark-cyan);
    }
}

.filter-part{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: white;

    border-radius: 4px;
    box-shadow: 0px 5px 20px 0px hsla(180, 29%, 50%, 0.3);

    margin: -2rem 1.5rem 0rem;
    padding: 1.2rem 1.1rem 1.2rem 1.2rem;

    @media screen and(min-width: 1024px) {
        margin: -2rem 11.5vw 3rem;
        padding: 1.2rem 2rem;
        border-radius: 6px;

        .filter-active{
            .filter{
                .b-filt:hover{
                    background-color: $very-cyan;
                }
            }
        }
    }

    .filter-active{
        display: flex;
        flex-wrap: wrap;
        max-width: 75%;
        gap: 0.8rem;

        .filter{
            display: flex;
            border-radius: 4px;
            overflow: hidden;
            

            span{
                padding: 0.6rem 0.6rem;
                background-color: $bg-gray;
                font-size: 16px;
                font-weight: 700;
                color: $dark-cyan;
                padding-right: 0.7rem;
            }

            .b-filt{
                background-color: $dark-cyan;
                cursor: pointer;
                border: none;
                width: 2.1rem;
                height: 100%;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
           

                div{
                    width: 1rem;
                    height: 0.15rem;
            
                    
                    background-color: white ;
                    border-radius: 6px;
                    position: absolute;
                    
                    
                    transform-origin: center;
                    transform: rotate(45deg);

                    &:last-of-type{
                        transform: rotate(-45deg);
                    }
                }
            }
        }
    }

    .clear{
        cursor: pointer;
        font-size: $f-size;
        font-weight: 700;
        letter-spacing: 0.5px;
        font-family: $font;
        color: $gray-cyan;

        &:hover{
            color: $dark-cyan;
            text-decoration: underline;
        }
    }
}


.job-offer{
    background-color: white;
    border-radius: 4px;
    margin: 3.5rem 1.5rem 2.5rem;

    padding: 2rem 1.5rem 1.5rem;
    box-shadow: 0px 10px 10px 0px hsla(180, 29%, 50%, 0.3);
    position: relative;


    &:last-of-type{
        margin-bottom: 0;
    }
}

#Photosnap{
    background-image: url('../images/photosnap.svg');
}

#Manage{
    background-image: url('../images/manage.svg');
}

#Account{
    background-image: url('../images/account.svg');
}

#MyHome{
    background-image: url('../images/myhome.svg');
}

#Loop-Studios{
    background-image: url('../images/loop-studios.svg');
}

#FaceIt{
    background-image: url('../images/faceit.svg');
}

#Shortly{
    background-image: url('../images/shortly.svg');
}

#Insure{
    background-image: url('../images/insure.svg');
}

#Eyecam-Co-{
    background-image: url('../images/eyecam-co.svg');
}

#The-Air-Filter-Company{
    background-image: url('../images/the-air-filter-company.svg');
}

.job-logo{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: -1.5rem;


}

.info{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;

    .name{
        display: flex;
        align-items: center;
        gap: 1.4rem;

        h4{
            font-size: $f-size;
            color: $dark-cyan;
        }

        span{
            padding: 0.35rem 0.6rem 0.18rem;
            border-radius: 15px;
            background-color: $dark-cyan;
            color: white;
            font-size: 13px;
            font-weight: 500;

            &:nth-of-type(2){
                background-color: $very-cyan;
                margin-left: 0.5rem;
            }
        }
    }

    h5{
        font-size: $f-size;
        color: $very-cyan;
        cursor: pointer;
    }

    .spec{
        display: flex;
        gap: 1.5rem;
        color: $gray-cyan;
        font-size: $f-size;
        border-bottom: 1px solid $gray-cyan;
        padding-bottom: 1.3rem;

        span::marker{
            margin: 0;
        }

        span:nth-of-type(2),span:nth-of-type(3){
            display: list-item;

            list-style: outside disc;

            
        }

    }

    .tech{
        display: flex;
        flex-wrap: wrap;
        gap: 0.8rem;

        p{
            padding: 0.6rem 0.6rem;
            border-radius: 4px;
            background-color: $bg-gray;
            font-size: 16px;
            font-weight: 700;
            color: $dark-cyan;
            padding-right: 0.7rem;
            cursor: pointer;
        }
    }
}

@media screen and (min-width: 1024px) {
    .job-logo{
        min-width: 88px;
        min-height: 88px;
        position: relative;
        top: unset;
    }

    .job-offer{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 2rem;
        margin: 1.5rem 11.5vw 1.5rem;
        padding: 1.5rem 2.5rem;
        border-radius: 8px;

        &:hover{
            border-left: 0.4rem solid $dark-cyan;
            padding-left: 2.1rem;
        }

        
    }

    .bg-top + .job-offer{
        margin-top: 4.5rem;
    }

    

    .info{
        display: grid;
        grid-template-columns: auto auto;
        grid-template-rows: 1fr 1fr 1fr;
        align-items: center;
        justify-content: space-between;
        row-gap: 0;

        .name{
            gap: 1rem;
            h4{
                font-size: 18px;
            }

            span{
                font-size: 15px;
            }
        }

        h5{
            grid-column: 1/2;
            grid-row: 2/3;
            font-size: 21px;
            &:hover{
                color: $dark-cyan;
            }
        }

        .spec{
            grid-column: 1/2;
            border-bottom: none;
            padding-bottom: 0;
            font-size: 18px;
            gap: 18px;

            span:nth-of-type(2),span:nth-of-type(3){
                list-style-position: inside ;
            }
        }

        .tech{
            grid-column: 2/3;
            grid-row: 2/3;

            p:hover{
                background-color: $dark-cyan;
                color: white;
            }
        }
    }
}