@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Start styling the attribution */
.attribution {
  position: fixed;
  bottom: 0rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.7rem;
  line-height: 1.6;
  text-align: center;
  color: hsl(220deg, 15%, 55%);
  width: 100%;
}

.attribution a {
  color: var(--Soft-blue);
}

.attribution a:hover {
  color: var(--Cyan);
  transition: color 0.3s;
}

/* Screen reader class to hide the content visually */
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

/*General and page initial setup*/
html {
  font-size: 100%;
  font-family: "League Spartan", sans-serif;
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
}

main {
  background-color: hsl(180deg, 52%, 96%);
  max-width: 100vw;
  min-height: 100vh;
  padding-bottom: 2.5rem;
}

.bg-top {
  display: block;
  width: 100%;
  height: 156px;
  background-image: url("../images/bg-header-mobile.svg"), linear-gradient(hsl(180deg, 29%, 50%), hsl(180deg, 29%, 50%));
  background-repeat: no-repeat;
  background-size: cover;
}
@media screen and (min-width: 1024px) {
  .bg-top {
    background-image: url("../images/bg-header-desktop.svg"), linear-gradient(hsl(180deg, 29%, 50%), hsl(180deg, 29%, 50%));
  }
}

.filter-part {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0px 5px 20px 0px hsla(180deg, 29%, 50%, 0.3);
  margin: -2rem 1.5rem 0rem;
  padding: 1.2rem 1.1rem 1.2rem 1.2rem;
}
@media screen and (min-width: 1024px) {
  .filter-part {
    margin: -2rem 11.5vw 3rem;
    padding: 1.2rem 2rem;
    border-radius: 6px;
  }
  .filter-part .filter-active .filter .b-filt:hover {
    background-color: hsl(180deg, 14%, 20%);
  }
}
.filter-part .filter-active {
  display: flex;
  flex-wrap: wrap;
  max-width: 75%;
  gap: 0.8rem;
}
.filter-part .filter-active .filter {
  display: flex;
  border-radius: 4px;
  overflow: hidden;
}
.filter-part .filter-active .filter span {
  padding: 0.6rem 0.6rem;
  background-color: hsl(180deg, 52%, 96%);
  font-size: 16px;
  font-weight: 700;
  color: hsl(180deg, 29%, 50%);
  padding-right: 0.7rem;
}
.filter-part .filter-active .filter .b-filt {
  background-color: hsl(180deg, 29%, 50%);
  cursor: pointer;
  border: none;
  width: 2.1rem;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.filter-part .filter-active .filter .b-filt div {
  width: 1rem;
  height: 0.15rem;
  background-color: white;
  border-radius: 6px;
  position: absolute;
  transform-origin: center;
  transform: rotate(45deg);
}
.filter-part .filter-active .filter .b-filt div:last-of-type {
  transform: rotate(-45deg);
}
.filter-part .clear {
  cursor: pointer;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 0.5px;
  font-family: "League Spartan", sans-serif;
  color: hsl(180deg, 8%, 52%);
}
.filter-part .clear:hover {
  color: hsl(180deg, 29%, 50%);
  text-decoration: underline;
}

.job-offer {
  background-color: white;
  border-radius: 4px;
  margin: 3.5rem 1.5rem 2.5rem;
  padding: 2rem 1.5rem 1.5rem;
  box-shadow: 0px 10px 10px 0px hsla(180deg, 29%, 50%, 0.3);
  position: relative;
}
.job-offer:last-of-type {
  margin-bottom: 0;
}

