.job-logo {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: -1.5rem;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.info .name {
  display: flex;
  align-items: center;
  gap: 1.4rem;
}
.info .name h4 {
  font-size: 15px;
  color: hsl(180deg, 29%, 50%);
}
.info .name span {
  padding: 0.35rem 0.6rem 0.18rem;
  border-radius: 15px;
  background-color: hsl(180deg, 29%, 50%);
  color: white;
  font-size: 13px;
  font-weight: 500;
}
.info .name span:nth-of-type(2) {
  background-color: hsl(180deg, 14%, 20%);
  margin-left: 0.5rem;
}
.info h5 {
  font-size: 15px;
  color: hsl(180deg, 14%, 20%);
  cursor: pointer;
}
.info .spec {
  display: flex;
  gap: 1.5rem;
  color: hsl(180deg, 8%, 52%);
  font-size: 15px;
  border-bottom: 1px solid hsl(180deg, 8%, 52%);
  padding-bottom: 1.3rem;
}
.info .spec span::marker {
  margin: 0;
}
.info .spec span:nth-of-type(2), .info .spec span:nth-of-type(3) {
  display: list-item;
  list-style: outside disc;
}
.info .tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
}
.info .tech p {
  padding: 0.6rem 0.6rem;
  border-radius: 4px;
  background-color: hsl(180deg, 52%, 96%);
  font-size: 16px;
  font-weight: 700;
  color: hsl(180deg, 29%, 50%);
  padding-right: 0.7rem;
  cursor: pointer;
}

@media screen and (min-width: 1024px) {
  .job-logo {
    min-width: 88px;
    min-height: 88px;
    position: relative;
    top: unset;
  }
  .job-offer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    margin: 1.5rem 11.5vw 1.5rem;
    padding: 1.5rem 2.5rem;
    border-radius: 8px;
  }
  .job-offer:hover {
    border-left: 0.4rem solid hsl(180deg, 29%, 50%);
    padding-left: 2.1rem;
  }
  .bg-top + .job-offer {
    margin-top: 4.5rem;
  }
  .info {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: 1fr 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    row-gap: 0;
  }
  .info .name {
    gap: 1rem;
  }
  .info .name h4 {
    font-size: 18px;
  }
  .info .name span {
    font-size: 15px;
  }
  .info h5 {
    grid-column: 1/2;
    grid-row: 2/3;
    font-size: 21px;
  }
  .info h5:hover {
    color: hsl(180deg, 29%, 50%);
  }
  .info .spec {
    grid-column: 1/2;
    border-bottom: none;
    padding-bottom: 0;
    font-size: 18px;
    gap: 18px;
  }
  .info .spec span:nth-of-type(2), .info .spec span:nth-of-type(3) {
    list-style-position: inside;
  }
  .info .tech {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .info .tech p:hover {
    background-color: hsl(180deg, 29%, 50%);
    color: white;
  }
}/*# sourceMappingURL=index.css.map */